<template>
    <div class="pt-10">
      <div class="bg-white border shadow h-auto p-2 relative">
        <div class="w-full">
          <QuillEditor v-if="is_edit"
            v-model:content="content"
            contentType="html"
            theme="snow"
            :options="editorOptions"
          ></QuillEditor>
          <div v-else v-html="translate_content ? translate_content : content" class="text-start !w-full block"></div>
        </div>
        <div v-if="is_edit" class="flex justify-between items-center w-full">
          <button @click="updateData" class="bg-green-500 text-white px-2 py-1 rounded mt-2">SAVE</button>
          <button @click="is_edit = false" class="bg-red-500 text-white px-2 py-1 rounded mt-2">CANCEL</button>
        </div>
        <div v-else class="flex justify-between items-center w-full mt-2">
          <div>
            <button @click="copyLink" class="bg-green-500 text-white w-8 h-8 rounded mr-1"><i class="fa-solid fa-share-from-square"></i></button>
            <button @click="is_edit = true" class="bg-red-500 text-white w-8 h-8 rounded"><i class="fa-solid fa-edit"></i></button>
          </div>
          <button @click="translateText" class="bg-blue-500 text-white w-8 h-8 rounded"><i class="fa-solid fa-arrows-turn-to-dots"></i></button>
        </div>
      </div>
    </div>
</template>

<script>
import { QuillEditor } from '@vueup/vue-quill'
import '@vueup/vue-quill/dist/vue-quill.snow.css';

export default {
  components: {
    QuillEditor
  },
  data() {
    return {
      is_edit: false,
      content: '',
      translate_content: '',
      copied: false,
      editorOptions: {
        modules: {
          toolbar: [
            [{ header: [1, 2, false] }],
            ['bold', 'italic', 'underline'],
            [{ list: 'ordered' }, { list: 'bullet' }],
            [{ script: 'sub' }, { script: 'super' }],
            [{ size: ['small', false, 'large', 'huge'] }],
            [{ color: [] }, { background: [] }],
            [{ font: [] }],
            [{ align: [] }],
            ['clean']
          ]
        }
      }
    }
  },
  created() {
    this.getEvent()
  },
  methods: {
    async getEvent() {
      const { data, error } = await this.$supabase.from("tips_tricks").select('*').eq("slug", "bear_hunt").single()
      this.content += data.content ? data.content : ''
    },
    async updateData() {
      try {
        const { error } = await this.$supabase.from("tips_tricks").update({
          content: this.content
        }).eq("slug", 'bear_hunt')
        this.$Swal.fire("", "Successfully", "success")
        this.is_edit = false
        if (error) {
          this.$Swal.fire("", "Error", "error")
          return
        }
      } catch (error) {
        this.$Swal.fire("", "Error Catch", "error")
      }
    },
    async copyLink() {
      try {
        await navigator.clipboard.writeText(window.location.href);
        this.$Swal.fire("", "Link successfully copied", "success")
      } catch (err) {
        console.error("Error", err);
      }
    },
    async translateText() {
      let text = this.content
      let targetLang = 'id'
      const url = `https://translate.googleapis.com/translate_a/single?client=gtx&sl=en&tl=${targetLang}&dt=t&q=${encodeURI(text)}`;
  
      const response = await fetch(url);
      const result = await response.json();
      
      let sx = result[0].map(item => item[0]).join("");
      this.translate_content = sx
      console.log(sx)
    }
  }
}
</script>

<style>
/* Tambahkan style untuk memastikan editor tampil dengan baik */
.ql-align-center {
  text-align: center !important;
}

.ql-editor {
  min-height: 150px;
  width: 90dvw;
}
</style>